"use client";

import { createContext, useContext, useMemo, useState } from "react";
import type { ReactNode } from "react";

export type AthleteReservation = {
  classId: number;
  reservedBy: "Athlete";
  createdAt: string;
};

type Ctx = {
  reservations: AthleteReservation[];
  reserve: (classId: number) => void;
  cancel: (classId: number) => void;
  isReserved: (classId: number) => boolean;
};

const ReservationsContext = createContext<Ctx | null>(null);

export function ReservationsProvider({ children }: { children: ReactNode }) {
  const [reservations, setReservations] = useState<AthleteReservation[]>([
    { classId: 2, reservedBy: "Athlete", createdAt: "2026-01-02 10:00" },
  ]);

  const reserve = (classId: number) => {
    setReservations((prev) => {
      if (prev.some((r) => r.classId === classId && r.reservedBy === "Athlete")) return prev;
      return [...prev, { classId, reservedBy: "Athlete", createdAt: "2026-01-02 10:00" }];
    });
  };

  const cancel = (classId: number) => {
    setReservations((prev) => prev.filter((r) => !(r.classId === classId && r.reservedBy === "Athlete")));
  };

  const isReserved = (classId: number) =>
    reservations.some((r) => r.classId === classId && r.reservedBy === "Athlete");

  const value = useMemo(
    () => ({ reservations, reserve, cancel, isReserved }),
    [reservations]
  );

  return <ReservationsContext.Provider value={value}>{children}</ReservationsContext.Provider>;
}

export function useReservations() {
  const ctx = useContext(ReservationsContext);
  if (!ctx) throw new Error("useReservations must be used within ReservationsProvider");
  return ctx;
}
