"use client";

import { useMemo, useState } from "react";
import Button from "@/components/ui/button/Button";
import { Modal } from "@/components/ui/modal";
import { useModal } from "@/hooks/useModal";
import { Table, TableBody, TableCell, TableHeader, TableRow } from "@/components/ui/table";
import { classesMock } from "../classes/classesMock";
import { useReservations } from "./reservationsStore";

export default function AthleteReservations() {
  const { reservations, cancel } = useReservations();

  const cancelModal = useModal();
  const [targetClassId, setTargetClassId] = useState<number | null>(null);

  const rows = useMemo(() => {
    const ids = reservations.map((r) => r.classId);
    return classesMock.filter((c) => ids.includes(c.id));
  }, [reservations]);

  const openCancel = (classId: number) => {
    setTargetClassId(classId);
    cancelModal.openModal();
  };

  const confirmCancel = () => {
    if (targetClassId == null) return;
    cancel(targetClassId);
    cancelModal.closeModal();
    setTargetClassId(null);
  };

  return (
    <div className="overflow-hidden rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
      <div className="flex flex-col justify-between gap-5 border-b border-gray-200 px-5 py-4 sm:flex-row sm:items-center dark:border-gray-800">
        <div>
          <h3 className="text-lg font-semibold text-gray-800 dark:text-white/90">
            Mis reservas
          </h3>
          <p className="text-sm text-gray-500 dark:text-gray-400">
            Reservas actuales (mock). Podés cancelar.
          </p>
        </div>
      </div>

      <div className="overflow-x-auto custom-scrollbar">
        <Table>
          <TableHeader className="border-y border-gray-200 dark:border-gray-800">
            <TableRow>
              <TableCell isHeader className="px-5 py-4 text-left text-xs font-medium text-gray-500 dark:text-gray-400">
                Clase
              </TableCell>
              <TableCell isHeader className="px-5 py-4 text-left text-xs font-medium text-gray-500 dark:text-gray-400">
                Día
              </TableCell>
              <TableCell isHeader className="px-5 py-4 text-left text-xs font-medium text-gray-500 dark:text-gray-400">
                Hora
              </TableCell>
              <TableCell isHeader className="px-5 py-4 text-left text-xs font-medium text-gray-500 dark:text-gray-400">
                Coach
              </TableCell>
              <TableCell isHeader className="px-5 py-4 text-left text-xs font-medium text-gray-500 dark:text-gray-400">
                Acción
              </TableCell>
            </TableRow>
          </TableHeader>

          <TableBody className="divide-y divide-gray-200 dark:divide-gray-800">
            {rows.map((row) => (
              <TableRow key={row.id} className="transition hover:bg-gray-50 dark:hover:bg-gray-900">
                <TableCell className="px-5 py-4 whitespace-nowrap">
                  <p className="text-sm font-medium text-gray-700 dark:text-gray-400">
                    {row.name}
                  </p>
                </TableCell>

                <TableCell className="px-5 py-4 whitespace-nowrap">
                  <p className="text-sm text-gray-500 dark:text-gray-400">
                    {row.day}
                  </p>
                </TableCell>

                <TableCell className="px-5 py-4 whitespace-nowrap">
                  <p className="text-sm text-gray-500 dark:text-gray-400">
                    {row.time}
                  </p>
                </TableCell>

                <TableCell className="px-5 py-4 whitespace-nowrap">
                  <p className="text-sm text-gray-500 dark:text-gray-400">
                    {row.coach}
                  </p>
                </TableCell>

                <TableCell className="px-5 py-4 whitespace-nowrap">
                  <Button size="sm" variant="outline" onClick={() => openCancel(row.id)}>
                    Cancelar
                  </Button>
                </TableCell>
              </TableRow>
            ))}

            {rows.length === 0 && (
              <TableRow>
                <TableCell colSpan={5} className="px-5 py-10 text-center text-sm text-gray-500 dark:text-gray-400">
                  No tenés reservas todavía (mock).
                </TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </div>

      <Modal
        isOpen={cancelModal.isOpen}
        onClose={cancelModal.closeModal}
        className="max-w-[600px] p-5 lg:p-10"
      >
        <div className="text-center">
          <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90 sm:text-title-sm">
            Cancelar reserva
          </h4>

          <p className="text-sm leading-6 text-gray-500 dark:text-gray-400">
            ¿Seguro querés cancelar esta reserva?
          </p>

          <div className="mt-7 flex w-full items-center justify-center gap-3">
            <button
              type="button"
              onClick={cancelModal.closeModal}
              className="flex justify-center rounded-lg border border-gray-300 bg-white px-4 py-3 text-sm font-medium text-gray-700 shadow-theme-xs hover:bg-gray-50 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-400 dark:hover:bg-white/5"
            >
              No, volver
            </button>

            <button
              type="button"
              onClick={confirmCancel}
              className="flex justify-center rounded-lg bg-error-500 px-4 py-3 text-sm font-medium text-white shadow-theme-xs hover:bg-error-600"
            >
              Sí, cancelar
            </button>
          </div>
        </div>
      </Modal>
    </div>
  );
}
